import {
  Button,
  Card,
  CardContent,
  Checkbox,
  Collapse,
  FormControl,
  FormControlLabel,
  FormGroup,
  Grid,
  MenuItem,
  Select,
  Slider,
  Typography
} from '@material-ui/core'
import ExpandMoreIcon from '@material-ui/icons/ExpandMore'
import clsx from 'clsx'
import { MAX_COOKING_TIME } from 'pages/RecipeCreate/constant'
import React, { useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { filterStyles, MARK_RANGE } from '../constant'

export default props => {
  const classes = filterStyles()
  const recipe = useSelector(state => state.Recipe)
  const dispatch = useDispatch()
  const [expanded, setExpanded] = useState({
    isExpanded: false,
    type: null
  })

  const {
    timeRange,
    setTimeRange,
    level,
    setLevel,
    food,
    setFood,
    sort,
    setSort,
    filterResult
  } = props

  const handleLevelChange = e => {
    const nextLevel = [...level]
    nextLevel[Number(e.target.name)].status = e.target.checked
    setLevel(nextLevel)
  }

  const handleFoodChange = e => {
    const nextfood = [...food]
    nextfood[Number(e.target.name)].status = e.target.checked
    setFood(nextfood)
  }

  const handleExpanded = type => {
    if (type === expanded.type) {
      setExpanded({ isExpanded: !expanded.isExpanded, type })
    } else {
      setExpanded({ isExpanded: true, type })
    }
  }

  const valuetext = value => {
    return `${value} phút`
  }

  return (
    <>
      <Grid container className={classes.root}>
        <Grid item xs={12} sm={6}>
          <Typography variant="h6">
            {props.name}{' '}
            <Typography variant="caption" className={classes.recipesNum}>
              {recipe.totalItems ? recipe.totalItems : 0} công thức
            </Typography>
          </Typography>
        </Grid>
        <Grid item xs={12} sm={6} className={classes.rightMenu}>
          <Typography variant="subtitle1">
            <strong>Sắp xếp theo:</strong>
            <FormControl>
              <Select
                label="Sắp xếp theo"
                variant="outlined"
                color="primary"
                value={sort}
                className={classes.sortMenu}
                onChange={e => {
                  setSort(e.target.value)
                  filterResult(e.target.value)
                }}
              >
                <MenuItem value="latest">Mới nhất</MenuItem>
                <MenuItem value="favorite">Yêu thích</MenuItem>
              </Select>
            </FormControl>
          </Typography>
        </Grid>
      </Grid>
      <Card className={classes.root}>
        <CardContent>
          <span className={classes.filterMenu}>
            <Button
              className={clsx(classes.expand, {
                [classes.expandOpen]: expanded.isExpanded
              })}
              onClick={() => handleExpanded('times')}
              aria-expanded={expanded.isExpanded}
              size="small"
              endIcon={<ExpandMoreIcon />}
            >
              Thời gian
            </Button>
          </span>
          <span className={classes.filterMenu}>
            <Button
              className={clsx(classes.expand, {
                [classes.expandOpen]: expanded.isExpanded
              })}
              onClick={() => handleExpanded('level')}
              aria-expanded={expanded.isExpanded}
              size="small"
              endIcon={<ExpandMoreIcon />}
            >
              Độ khó
            </Button>
          </span>
          <span className={classes.filterMenu}>
            <Button
              className={clsx(classes.expand, {
                [classes.expandOpen]: expanded.isExpanded
              })}
              onClick={() => handleExpanded('food')}
              aria-expanded={expanded.isExpanded}
              size="small"
              endIcon={<ExpandMoreIcon />}
            >
              Ẩm thực
            </Button>
          </span>
          <span>
            <Button
              onClick={() => filterResult(sort)}
              aria-expanded={expanded.isExpanded}
              size="small"
              variant="contained"
              color="primary"
            >
              Áp dụng
            </Button>
          </span>
          <Collapse in={expanded.isExpanded} timeout="auto" unmountOnExit>
            <FormControl component="fieldset">
              {expanded.type === 'times' ? (
                <Slider
                  value={timeRange}
                  min={1}
                  max={MAX_COOKING_TIME}
                  onChange={(event, value) => setTimeRange(value)}
                  valueLabelDisplay="auto"
                  aria-labelledby="range-slider"
                  getAriaValueText={valuetext}
                  className={classes.range}
                  marks={MARK_RANGE}
                />
              ) : expanded.type === 'level' ? (
                <FormGroup row>
                  {level.map((item, index) => (
                    <FormControlLabel
                      key={item.code}
                      control={
                        <Checkbox
                          checked={item.status}
                          color="primary"
                          onChange={handleLevelChange}
                          name={index.toString()}
                        />
                      }
                      label={item.title}
                    />
                  ))}
                </FormGroup>
              ) : (
                <FormGroup row>
                  {food.map((item, index) => (
                    <FormControlLabel
                      key={item.code}
                      control={
                        <Checkbox
                          checked={item.status}
                          color="primary"
                          onChange={handleFoodChange}
                          name={index.toString()}
                        />
                      }
                      label={item.title}
                    />
                  ))}
                </FormGroup>
              )}
            </FormControl>
          </Collapse>
        </CardContent>
      </Card>
    </>
  )
}
